.container {
  position: relative;
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;

  @include for-size(phone) {
    width: 480px;
  }
  @include for-size(tablet) {
    width: 768px;
    padding-right: 34px;
    padding-left: 34px;
  }
  @include for-size(desktop) {
    width: 1280px;
    padding-right: 115px;
    padding-left: 115px;
  }

  // outline: 1px solid tomato;
}
